/* Reset b√°sico */
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    font-family: 'Segoe UI', Arial, sans-serif;
    background: #f4f6fa;
    color: #222;
    min-height: 100vh;
}

/* Contenedor principal */
.app-container {
    max-width: 600px;
    margin: 40px auto;
    background: #fff;
    border-radius: 12px;
    box-shadow: 0 2px 16px rgba(0,0,0,0.07);
    padding: 32px 24px;
}

/* Encabezado */
h1 {
    font-size: 2rem;
    margin-bottom: 24px;
    color: #2d6cdf;
    text-align: center;
}

/* Lista de tareas */
.task-list {
    list-style: none;
    margin-top: 24px;
}

.task-item {
    display: flex;
    align-items: center;
    justify-content: space-between;
    background: #f9fafc;
    border-radius: 8px;
    padding: 14px 18px;
    margin-bottom: 12px;
    transition: background 0.2s;
}

.task-item.completed {
    text-decoration: line-through;
    color: #aaa;
    background: #e6eaf1;
}

.task-title {
    flex: 1;
    font-size: 1.05rem;
}

/* Botones */
button,
input[type="submit"] {
    background: #2d6cdf;
    color: #fff;
    border: none;
    border-radius: 6px;
    padding: 8px 16px;
    font-size: 1rem;
    cursor: pointer;
    transition: background 0.2s;
}

button:hover,
input[type="submit"]:hover {
    background: #1b4fa0;
}

.delete-btn {
    background: #e74c3c;
    margin-left: 10px;
}

.delete-btn:hover {
    background: #c0392b;
}

/* Formulario de nueva tarea */
.add-task-form {
    display: flex;
    gap: 12px;
    margin-bottom: 18px;
}

.add-task-input {
    flex: 1;
    padding: 8px 12px;
    border: 1px solid #cfd8dc;
    border-radius: 6px;
    font-size: 1rem;
    outline: none;
    transition: border 0.2s;
}

.add-task-input:focus {
    border-color: #2d6cdf;
}

/* Responsive */
@media (max-width: 700px) {
    .app-container {
        max-width: 98vw;
        padding: 18px 6px;
    }
}